>

<template>
  <div class="col-md-12 p-0 listing">
    <loader :is-visible="isLoading"></loader>
        <TgList IsShowAction="false"
            ModuleName="CRM" 
            SubModuleCode="CRM_COMMUNICATION_MODES" 
            IdentityColumn="modeTypeId"
            :HeaderText="$t('CommunicationModes')"
            :ListData="CommunicationModesData" 
            :HeaderData="Headers"
            :HeaderButtons="listHeaderButtons" 
            :TotalRecords="TotalRecords"
            :ListDataCallBackFunction="FetchData"
            :SearchFields="leftSearchFields"
            :IsFilterApplied="searchCondition?true:false"
            :SortExp="SortExp"
            :SortBy="SortBy"
            :RenderRowActionMethod="renderActions"
            v-on:LeftsearchButtonClick="SearchMode"
            v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{data}">              
                <template v-if="data.column.COLUMN_NAME == 'status'">
                    <listing-status  :isfor="'CRM_COMMUNICATION_MODES'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM_COMMUNICATION_MODES')" ref="updatestatus" v-bind:dataid="data.row.modeTypeId" 
                    data-controller="Crm" v-bind:statuslist="StatusListForListing" v-bind:item="data.row" data-action="index"  v-bind:dataselected="data.row.status" v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true" 
                   >
                    </listing-status>
                    <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_COMMUNICATION_MODES')">
                        <span v-bind:class="getStatusClass(data.row.status_id, 'STATUS_ID')">
                            {{data.row[data.column.COLUMN_NAME]}}
                        </span>
                    </a>
                </template>
        </template>
    </TgList>
    </div>

</template>



<script>
import CommunicationModesDataService from "../../../services/DataService";
import Schema from "./CommunicationModesListingSchema";
import TgList from '../../../components/Common/TgList.vue';
import $ from "jquery";
export default {
    components: {
        TgList
    },
    data() {
        return {
            
            CommunicationModesData: '',
            isLoading: false, 
            listHeaderButtons: Schema.buttons,
            listActions: Schema.listActions,
            userid: null,
            TotalRecords:null,
            usertype:null,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            searchCondition:'',
            SortExp: '',
            PageSizeOption: null,
            CurrentPage: 1,
            TotalPages: 0,
            noRecord: false,
            leftSearchFields:Schema.leftSearchSchema,
            StatusListForListing: [{
                STATUS: 'Active',
                STATUS_ID: 1001,
                STATUS_BG_COLOR: "#28a745",
                STATUS_COLOR: "#FFFFFF"
            }, {
                STATUS: 'Inactive',
                STATUS_ID: 1002,
                STATUS_BG_COLOR: "#bf4b4b",
                STATUS_COLOR: "#FFFFFF"
            }],
            Headers: [
                {
                    "COLUMN_NAME":"communicationTypeName",
                    "DATA_TYPE":"string",
                    "DT_CODE":"text",
                    "SORTABLE":true,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":1,
                    "DISPLAY_NAME":"Name", 
                    settings: {
                        clickEvent: this.clickEventOfColumn
                    }
                },
                {
                    "COLUMN_NAME":"communicationTypeDescription",
                    "DATA_TYPE":"string",
                    "DT_CODE":"textarea",
                    "SORTABLE":true,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":2,
                    "DISPLAY_NAME":"Description"
                },
                {
                    "COLUMN_NAME":"status",
                    "DATA_TYPE":"select",
                    "DT_CODE":"select",
                    "SORTABLE":true,
                    "VISIBLE":true,
                    "DISPLAY_ORDER":3,
                    "DISPLAY_NAME":"Status",
                    settings: {
                        isInSlot: true
                    }
                }
            ]
        }
    },
    created: function () {
  this.userid = this.GetUserInfo.ID
   this.usertype = this.GetUserInfo.UserType
        var ths = this;
        ths.FetchData();
    },
    methods: {
        renderActions(){

        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        getRequestParams: function (search, pageSize, pageNum, sortBy, sortExp) {
            let params = {};
            if (search) { params["search"] = search; }
            if (pageSize) { params["pageSize"] = pageSize; }
            if (pageNum) { params["pageNum"] = pageNum; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortBy) { params["sortBy"] = sortBy; }
            return params;
        },
        clickEventOfColumn:function(row, column){
                if(column.COLUMN_NAME == 'communicationTypeName'){
                    this.$router.push({ path: '/CRM/ManageCommunicationModes/'+row.modeTypeId+'/yes' });
                }
        },
        FetchData: function () {
            const params = this.getRequestParams(this.search, this.pageSize, this.pageNum, this.sortBy, this.sortExp);
            var vueObj = this;
            this.isLoading = true;
            params.pageSize = vueObj.PageSize;
            params.pageNum = vueObj.PageNumber;
            params.search = "";
            if (vueObj.PageNumber) { params.pageNum = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNum = 1; }
            params.sortBy = vueObj.SortBy;
            params.sortExp = vueObj.SortExp;
            CommunicationModesDataService.GetCommunicationModesList(params).then(function (response) {
                vueObj.isLoading = false;
                if (response.data != "" && response.data != null) {
                    if (response.data.result.length > 0) {
                        vueObj.CommunicationModesData = response.data.result;
                        vueObj.CommunicationModesData.forEach((row)=>{
                            row.rowDetailsUrl='';
                            if(row.rowDetailsUrl=='')
                            {
                                row.rowDetailsUrl='/CRM/ManageCommunicationModes/'+row.modeTypeId +'/yes';
                            }
                            });
                        vueObj.TotalRecords = response.data.result[0].total ;
                        vueObj.TotalPages = Math.ceil(response.data.result[0].total / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.CommunicationModesData = [];
                        vueObj.TotalRecords = 0
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.CommunicationModesData = [];
                    vueObj.Headers = [];
                    vueObj.TotalRecords = 0
                }
                setTimeout(function(){
                    vueObj.ResponsiveDataTable('tablelistingdata')
                }, 500);
                vueObj.isLoading = false;
            });            
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "BulkStatusUpdate(1001)":
                    this.BulkStatusUpdate(1001);
                    break;
                case "BulkStatusUpdate(1002)":
                    this.BulkStatusUpdate(1002);
                    break;

                    const removeFilterindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    ths.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    ths.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                    try {
                        ths.appliedFilter = [];
                    } catch (e) { };
                    ths.FetchData();
                    break;
            }
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            var str = "";
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("CommunicationModes")), function () {
                    str += val + ',';
                    CommunicationModesDataService.CommunicationModesDelete('?ids=' + str).then(function (response) {
                        var box = "";
                        if (response.data == "success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='ChangeOwnerId']").removeClass('enable').addClass('disabled');
                            $("[id^='AdditionalOwnerId']").removeClass('enable').addClass('disabled');
                            $("[id^='CloseLeadId']").removeClass('enable').addClass('disabled');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            box = vm.ShowAlert(vm.$t('DeletedSuccessfully', [vm.$t('CommunicationModes')]), "success", true, vm.$t("Alert"));                            
                            vm.PageNumber=parseInt(vm.PageNumber)-1;
                            if(vm.PageNumber<=0){
                                vm.PageNumber=1;
                            } 
                            vm.FetchData(); 
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('CommunicationModes')]), "failure", true, vm.$t("Alert"));                                
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);
                         
                    },);
                });
            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete'), "warning", true, vm.$t("Alert"));
            }
        },
        DrpdownUpdataStatus: function (event, s) {
            var vm = this;
            var params = "?id=" + event + "&statusId=" + s;
            CommunicationModesDataService.CommunicationModesUpdateStatus(params).then(function (response) {
                var box = "";
                if (response.data.status == "success") {
                    $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                    $("[id^='ChangeOwnerId']").removeClass('enable').addClass('disabled');
                    $("[id^='AdditionalOwnerId']").removeClass('enable').addClass('disabled');
                    $("[id^='CloseLeadId']").removeClass('enable').addClass('disabled');
                    $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                    vm.FetchData();
                    box = alert(vm.$t('DeletedSuccess'), vm.$t('CommunicationModes'));
                }
                else {
                    box = alert(vm.$t('DeletedError'), vm.$t('CommunicationModes'));
                }
            });
        },
        BulkStatusUpdate: function (StatusId) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            var str = "";
            if (val.length > 0) {
                vm.confirmR(vm.$t("StatusChange"), true, vm.$t("CommunicationModes"), function () {
                    str += val + ',';
                    var params = "?id=" + str + "&statusId=" + StatusId;
                    CommunicationModesDataService.CommunicationModesUpdateStatus(params).then(function (response) {
                        var box = "";
                        if (response.data.status == "success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='ChangeOwnerId']").removeClass('enable').addClass('disabled');
                            $("[id^='AdditionalOwnerId']").removeClass('enable').addClass('disabled');
                            $("[id^='CloseLeadId']").removeClass('enable').addClass('disabled');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.FetchData();
                            box = vm.ShowAlert(vm.$t('StatusChangeSuccess'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('StatusChangeError'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);
                    },);
                });
            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete'), "warning", true, vm.$t("Alert"));
            }
        },
        SearchMode: function (SearchValue) {
            var SearchTextVal = SearchValue.searchCondition !='' ? (SearchValue.searchCondition.split('%')[1]).split('%')[0] : SearchValue.searchCondition='' ;
            const params = this.getRequestParams(this.search, this.pageSize, this.pageNum, this.sortBy, this.sortExp);
            var vueObj = this;
            this.isLoading = true;
            params.pageSize = vueObj.PageSize;
            params.pageNum = vueObj.PageNumber;
            params.search = SearchTextVal;
            if (vueObj.PageNumber) { params.pageNum = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNum = 1; }
            params.sortBy = vueObj.SortBy;
            params.sortExp = vueObj.SortExp;
            CommunicationModesDataService.GetCommunicationModesList(params).then(function (response) {
                vueObj.isLoading = false;
                if (response.status == 200) {
                    vueObj.CommunicationModesData = response.data.result;
                    vueObj.TotalRecords = response.data.result.length;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                    vueObj.CurrentPage = vueObj.PageNumber;
                    vueObj.noRecord = false;
                } else {
                    vueObj.noRecord = true;
                    vueObj.CommunicationModesData = [];
                    vueObj.TotalRecords = 0
                }
                setTimeout(function(){
                    vueObj.ResponsiveDataTable('tablelistingdata')
                }, 500);
            },
            );
        },
        RefreshSearch: function (s) {
            this.FetchData();
        }
    },
}
</script>
